import Axios from "../../../api/Axios";
import Form from "../../../components/Form/index.tsx";
import PicUpload from "../../../components/PicUpload/index.tsx";
import { BtnWrapper, Btn, BtnSecondary, Column as Col, Row, Title } from "../../../components/StyledComponent/index.tsx";
import { AiOutlineCloseCircle } from "react-icons/ai";
import { IoMdAddCircleOutline } from "react-icons/io";
import TextInput from "../../../components/TextInput/index.tsx";
import { useState, useRef, useEffect, useCallback } from "react";
import Loader from "../../../components/Loader";
import { useNavigate, useParams } from "react-router-dom";
import Select from "../../../components/Select/index.tsx";
import triggerSwalAlert from "../../../helpers/triggerSwalAlert";

const axios = new Axios();

const AddEditPopup = (props) => {

  const [pending, setPending] = useState(false);
  let [details, setDetails] = useState({ image: {}})
  const navigate = useNavigate()
  const { id } = useParams();
  const [chooseAttribute, setChooseAttribute] = useState([])
  const formRef = useRef();
  let [formValues, setFormValues] = useState([
    { productName: "", attributeValue: "", count: "" , pictureUrl: "", productMainImageProgress: 0, refNumber: "", hsnNumber: "" },
  ]);
  const addFormFields = () => {
    setFormValues([
      ...formValues,
      { productName: "", attributeValue: "", count: "", pictureUrl: "", productMainImageProgress: 0, refNumber: "", hsnNumber: "" },
    ]);
  };

  const removeFormFields = async (i) => {
    let formValue = formValues[i]
    let promiseArray = []
    if (formValue.pictureUrl)
    promiseArray.push(multiplePicturesDeletion(formValue.pictureUrl))
    await Promise.all(promiseArray)
    formValues.splice(i, 1);
    setFormValues([...formValues]);
  };

  const multiplePicturesDeletion = (image) => {
    let splitImage = image.split("/")
    let imageName = splitImage[splitImage.length - 1]
    const options = {
      method: "DELETE",
      body: {
        name: imageName
      }
    }
    return axios.callApi(`/file/deleteFile`, options)
  }

  const getPopupDetails = useCallback(async () => {
    setPending(true)
    let options = {
      method: "GET"
    }
    let result = await axios.callApi(`/popup/getPopupById/${id ? id : ""}`, options);
    if (result?.status == 200) {
      result.data.data.image.progress = 100
      details = result.data.data
      setDetails(result.data.data)
      let tempFormValues = result?.data?.data?.products.map((productVariant)=> {
        productVariant.productMainImageProgress = 100
        productVariant.attributeValue = Object.values(productVariant.attributeCombination)[0]
        return productVariant
      })
      setFormValues(tempFormValues)
      setPending(false)
    }
  }, [])

  useEffect(() => {
    if(props.type == "edit"){
      getPopupDetails()
    }
    getAllProductAttributes()
  }, [])

  const handlePopupImageChange = async (value) => {
    let formData = new FormData();
    formData.append("productImage", value);
    const options = {
      method: "POST",
      headers: {
        "Content-Type": "multipart/form-data"
      },
      body: formData,
      onUploadProgress: (data) => {
        details.image.progress = Math.round((100 * data.loaded) / data.total);
        setDetails(JSON.parse(JSON.stringify(details)))
      },
    }
    let result = await axios.callApi("/file/uploadFile", options)

    if (result.status == 200) {
      details.image.url = result?.data?.data
      setDetails(JSON.parse(JSON.stringify(details)))
    }
    else {
      details.image.progress = -1
      setDetails(JSON.parse(JSON.stringify(details)))
    }
  };
  const handlePopupImageDelete = async () => {
    setPending(true);
    let selectedImage = details?.image?.url
    let splitImage = selectedImage.split("/")
    let imageName = splitImage[splitImage.length - 1]
    const options = {
      method: "DELETE",
      body: {
        name: imageName
      }
    }
    let result = await axios.callApi(`/file/deleteFile`, options)
    if (result?.status === 200) {
      setPending(false);
      details.image.url = ""
      details.image.progress = 0
      setDetails(JSON.parse(JSON.stringify(details)))
      return "success";
    }
    else {
      setPending(false);
      return "failed"
    }
  };

  const getAllProductAttributes = useCallback(async () => {
    //setPending(true)
    let options = {
      method: "GET"
    }
    let result = await axios.callApi("/addProduct/chooseProductAttributes", options);
    if (result?.status == 200) {
      //setPending(false)
      setChooseAttribute(result?.data?.data.map((item) => {
        return {
          value: item.id,
          label: item.attributeName
        }
      }))
    }
  }, [])

  const handleProductMainImageChange = async (value, productVariantIndex) => {

    let formData = new FormData();
    formData.append("productImage", value);
    const options = {
      method: "POST",
      headers: {
        "Content-Type": "multipart/form-data"
      },
      body: formData,
      onUploadProgress: (data) => {
        formValues[productVariantIndex].productMainImageProgress = Math.round((100 * data.loaded) / data.total);
        setFormValues([...formValues])
      },
    }
    let result = await axios.callApi("/file/uploadFile", options)

    if (result.status == 200) {
      formValues[productVariantIndex].pictureUrl = result?.data?.data
      setFormValues([...formValues])
    }
    else {
      formValues[productVariantIndex].productMainImageProgress = -1
      setFormValues([...formValues])
    }

  };
  const handleProductMainImageDelete = async (productVariantIndex) => {
    setPending(true);
    let selectedImage = formValues[productVariantIndex].pictureUrl
    let splitImage = selectedImage.split("/")
    let imageName = splitImage[splitImage.length - 1]
    const options = {
      method: "DELETE",
      body: {
        name: imageName
      }
    }
    let result = await axios.callApi(`/file/deleteFile`, options)
    if (result?.status === 200) {
      setPending(false);
      formValues[productVariantIndex].pictureUrl = ""
      formValues[productVariantIndex].productMainImageProgress = 0
      setFormValues([...formValues])
      return "success";
    }
    else {
      setPending(false);
      return "failed"
    }
  };

  const submit = async (value) => {
    setPending(true);
    const {
     minimumAmount
    } = value;

    const payload = {
      minimumAmount,
      image: {url: details.image.url}
    }

    let products = []

    for (let i = 0; i < formValues.length; i++) {
      let obj = {}
      obj.refNumber = value[`refNumber${i}`]
      obj.hsnNumber = value[`hsnNumber${i}`]
      obj.productName = value[`productName${i}`]
      obj.count = value[`count${i}`]
      obj.pictureUrl = formValues[i].pictureUrl
      obj["attributeCombination"] = {}
      obj["attributeCombination"][value[`attribute${i}`]] = value[`attributeValue${i}`]
      products.push(obj)
    }

    payload["products"] = products

    if(props.type == "edit"){
      payload["id"] = details.id
    }

    let options = {
      method: (props.type == "edit") ? "PUT" : "POST",
      body: payload
    }

    let url = (props.type == "edit") ? "/popup/updatePopup" : "/popup/createPopup"

    let result = await axios.callApi(url, options);

    if (result?.status === 200) {
      let message = (props.type == "edit") ? "Updated successfully" : "Popup successfully created"
      triggerSwalAlert("Success", message, "success" )
      setPending(false);
    } else {
      triggerSwalAlert("Error", "Something went wrong", "error" )
      setPending(false);
    }

    navigate("/manage-popups")

  };
  return (
    <>
      {pending ? <Loader /> : null}
      <div className="container-fluid addEditProductContainer my-lg-5">
        <Form onSubmit={submit} ref={formRef}>
          <Row>
            <Col md={12}>
              <TextInput
                name="minimumAmount"
                label="Minimum Amount"
                //imageSrc={Company}
                required
                isFormInputElement
                value={details?.minimumAmount}
              // maxLength={200}
              />
            </Col>
            <Col md={12}>
              <PicUpload
                name={`image`}
                label="Popup Image"
                //imageSrc={image}
                isFormInputElement
                required
                acceptedFormat="image/png, image/jpeg, image/jpg"
                onChange={(value)=>handlePopupImageChange(value)}
                value={details?.image?.url ? process.env.REACT_APP_BASE_URL + "/" + details?.image?.url : ""}
                deleteFile={handlePopupImageDelete}
                progress={details?.image?.progress}
              />
            </Col>
            <Col md={12}>
              <Title>Add Products</Title>

              {formValues.map((element, index) => (
                <div key={index}>
                  <Col md={12}>
                    <div className="form-title">
                      <p>Product {index + 1} Details</p>
                      {index ? (
                        <p onClick={() => removeFormFields(index)}>
                          <AiOutlineCloseCircle />
                          Delete Product {index + 1}
                        </p>
                      ) : null}
                    </div>
                  </Col>
                  <Col md={6}>
                    <TextInput
                      name={`productName${index}`}
                      label="Product Name"
                      //imageSrc={HA}
                      value={element.productName}
                      required
                      isFormInputElement
                    />
                  </Col>
                  <Col md={6}>
                    <Select
                      name={`attribute${index}`}
                      label="Choose Attribute"
                      //imageSrc={Time}
                      required
                      isFormInputElement
                      value={Object.keys(element?.attributeCombination || {})[0]}
                      data={chooseAttribute}
                    />
                  </Col>
                  <Col md={6}>
                    <TextInput
                      name={`attributeValue${index}`}
                      label="Attribute Value"
                      //imageSrc={HC}
                      value={element.attributeValue}
                      required
                      isFormInputElement
                    />
                  </Col>
                  <Col md={6}>
                    <TextInput
                      name={`count${index}`}
                      label="Product Count"
                      //imageSrc={HB}
                      value={element.count}
                      required
                      isFormInputElement
                    />
                  </Col>
                  <Col md={6}>
                    <TextInput
                      name={`refNumber${index}`}
                      label="Reference Number"
                      //imageSrc={HC}
                      value={element.refNumber}
                      required
                      isFormInputElement
                    />
                  </Col>
                  <Col md={6}>
                    <TextInput
                      name={`hsnNumber${index}`}
                      label="Hsn Number"
                      //imageSrc={HC}
                      value={element.hsnNumber}
                      required
                      isFormInputElement
                    />
                  </Col>
                  <Col md={12}>
                    <PicUpload
                      name={`productImage${index}`}
                      label="Product Main Image(single pic)"
                      //imageSrc={image}
                      isFormInputElement
                      required
                      productVariantIndex={index}
                      acceptedFormat="image/png, image/jpeg, image/jpg"
                      onChange={handleProductMainImageChange}
                      value={element.pictureUrl ? process.env.REACT_APP_BASE_URL + "/" + element.pictureUrl : ""}
                      deleteFile={handleProductMainImageDelete}
                      progress={element.productMainImageProgress}
                    />
                  </Col>
                </div>
              ))}
            </Col>
          </Row>
          <p className="link-text" onClick={() => addFormFields()}>
            <IoMdAddCircleOutline /> Add Another Product
          </p>
          <BtnWrapper>
            <div>
              <BtnSecondary
                style={{ marginRight: 12 }}
              >
                Cancel
              </BtnSecondary>
              <Btn
                disabled={false}
                onClick={() => formRef.current?.handleSubmit()}
              >
                {false ? <i className="fas fa-spinner fa-pulse"></i> : "Submit"}
              </Btn>
            </div>
          </BtnWrapper>
        </Form>
      </div>
    </>
  )
}

export default AddEditPopup;